.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

footer{
  justify-content: center;
  width: 100%;
}

footer > #navbar{
  display: flex;
  background-color: rgb(255, 255, 255);
  width: 100%;
}

footer > #navbar > div > .navButton{
  background-color: white;
  border: none;
  border-bottom: solid;
  color: blue;
  padding-left: 0%;
  padding-right: 0%
}

#app-content{
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: space-between;
}

#page-content{
  height: 100%;
  justify-content: center;
}